<template>
  <article v-if="productsData">
    <button type="button" class="btn btn-primary mb-3" @click="openModal(-1)">新增產品</button>
    <Modal :propEditProductId="editProductId"/>
    <section>
      <table class="table table-hover">
        <thead>
          <tr>
            <th scope="col">編號</th>
            <th scope="col">圖片</th>
            <th scope="col">標題</th>
            <th scope="col">性別</th>
            <th scope="col">種類</th>
            <th scope="col">價格</th>
            <th scope="col">修改</th>
            <th scope="col">刪除</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(product,index) in productsData" :key="index">
            <th scope="row">{{product.product_id}}</th>
            <td>
              <img :src="imgUrl(product.product_id)" class="product_img">
            </td>
            <td>{{product.title}}</td>
            <td>{{product.gender}}</td>
            <td>{{product.category}}</td>
            <td>{{product.price}}</td>
            <td>
              <button class="btn btn-primary" @click="openModal(product.product_id)">修改</button>
            </td>
            <td>
              <button class="btn btn-primary" @click="deleteProduct(product.product_id)">刪除</button>
            </td>
          </tr>
        </tbody>
      </table>
    </section>
  </article>
</template>
<script>
import AxiosClass from "@/config/axiosClass.js";
import $ from "jquery";
import Modal from "./admin_product_modal.vue";
export default {
  components: {
    Modal
  },
  data() {
    return {
      productsData: [],
      editProductId: -1,
      isRenderModal:false
    };
  },
  methods: {
    openModal(id = -1) {
      this.editProductId = id;
      $("#exampleModalCenter").modal("show");
    },
    cleanTempData() {
      this.tempData = this.emptyData;
      this.colorNum = 0;
    },
    imgUrl(product_id) {
      return `${process.env.VUE_APP_BACKURL}img/${product_id}/1.jpg`;
    },
    getProducts() {
      const getProducts = new AxiosClass("admin/product", "get", null, true);
      getProducts.doAxios(
        this,
        res => {
          this.productsData = res.data.data;
          this.$emit("push-alert", "抓取商品成功");
        },
        () => this.$emit("push-alert", "抓取商品失敗", true)
      );
    },
    deleteProduct(product_id) {
      if (confirm(`確定刪除 編號${product_id} 產品嗎`) == true) {
        const deleteProduct = new AxiosClass(
          `admin/product/${product_id}`,
          "delete",
          null,
          true
        );
        deleteProduct.doAxios(
          this,
          res => {
            this.$emit("push-alert", `刪除 編號${product_id} 商品成功`);
            this.getProducts();
          },
          res => {
            this.$emit("push-alert", `刪除 編號${product_id} 商品失敗`, true);
          }
        );
      }
    },
    modifyProduct(product_id) {
      this.editProductId = product_id;
      this.openModal();
    }
  },
  created() {
    this.getProducts();
  }
};
</script>

<style lang="scss" scoped>
.product_img {
  height: 50px;
}
</style>
